<template>
<div>
<mt-popup v-model="initVisible" position="right" :modal="false" style="width:100%;height:100%;">

<mt-checklist
      class="page-part"
      title="最近感觉如何"
      v-model="value1"
      :options="options1">
    </mt-checklist>

<mt-button type="primary" @click="initVisible = false" style="height:50px;width:200px;background:#bbbbbb;position: fixed;
    top: 600px;
    margin-left: 100px;">
选好了
</mt-button>
</mt-popup>
<mt-tab-container v-model="selected">
  <mt-tab-container-item id="bubble" class="bubble">
  <img src="./assets/bubble_bg.png" style="left: 0px;top: 0px;position:fixed;height:800px">
  <mt-button type="primary" @click="inputPopupVisible = true" style="height:50px;width:200px;background:#bbbbbb;position: fixed;
    top: 600px;
    margin-left: 100px;">
<img src="./assets/bubble_blowing.png" height="24" width="28" slot="icon">
  吹一个泡泡</mt-button>
  <div v-for="bubble in bubbles" @click="toggle_bubble" class="bubble-cell"><p style="margin-top: 30px;">{{ bubble }}</p></div>

  <mt-popup v-model="inputPopupVisible" position="bottom" class="mint-popup" :modal="false" id="bubble_send">
  <mt-field label="" placeholder="说点什么" type="textarea" rows="3" style=""></mt-field>
    <mt-button @click="blow" size="large" type="primary">发送</mt-button>
  </mt-popup>
  <mt-popup v-model="popupVisible" position="right" class="mint-popup":modal="false">
    <div id="chatWindow" style="height: 300px;width: 240px;">
    <mt-cell id="content" style="height: 140px;width:220px;margin-top: 10px;margin-right: 10px;margin-left:10px">在公司参加Hackathon，东西做不完了淦。。。</mt-cell>
    <div id="bubble-stats" style="height: 60px;width:240px;text-align:right;font-style: italic;" v-model="hug_cnt">
    <p style="padding-right: 10px">得到了{{hug_cnt}}个抱抱</p>
    </div>
    <div id="bubble-nav" style="height: 60px;width:240px">
    <center>
    <mt-button @click.native="hug_cnt++;" id="hug_button">
      <img src="./assets/hug_icon.png" height="24" width="28" slot="icon">
    </mt-button>
    <mt-button @click.native="popupVisible2 = true">评论</mt-button>
    <mt-button @click.native="popupVisible4 = true">聊聊</mt-button>
    </center>
    </div>
    <mt-button @click="release" size="large" type="primary">收起</mt-button>
    </div>
  </mt-popup>
  <mt-popup v-model="popupVisible2" position="right" :modal="false" style="height:500px;width:300px;">
    <div style="height:350px;max-height: 350px;overflow-y: scroll;border: solid;" id="comment_ul">
    <mt-cell v-for="comment in commentList" :title="comment.user + ':'" :value="comment.message">{{ comment.message }}</mt-cell>
    
    </div>
    <mt-field label="" placeholder="说点什么" type="textarea" rows="3" id="commentArea"></mt-field>
    <mt-button @click="append" style="left: 230px;position: relative;bottom: 50px;">发送</mt-button>
    <mt-button @click.native="release" size="large" type="primary" style="position: fixed;bottom: 0px;">不看了</mt-button>
  </mt-popup>

  <mt-popup v-model="popupVisible4" position="right" :modal="false" style="width:100%;height:100%">
    <div class="messageList">
      
    </div>
    <mt-field label="" placeholder="说点什么" type="textarea" rows="3" id="chat" style="
    position: fixed;
    height: 100px;
    top: 500px;
    border: solid;
    width: 100%;
"></mt-field>
    <mt-button @click.native="send_msg" size="large" type="primary" style="position: fixed;bottom: 50px;">发送</mt-button>
    <mt-button @click.native="popupVisible4 = false" size="large" type="primary" style="position: fixed;bottom: 0px;">不聊了先</mt-button>
  </mt-popup>
  </mt-tab-container-item>
  <mt-tab-container-item id="dungeon">
  <div class="recruitment_list">
    <p style="text-align:center;font-weight: bold;font-size: 30px;">招募中的队伍</p>
    <div class="recruitment" v-for="rec in recruitments" style="margin-left: 80px;margin-right: 80px;height:105px; border-style: solid;margin-bottom: 50px;">
      <div class="recImg" style="display: inline-block;top: 10px;position: relative;left: 5px;">
        <img :src="rec.imgPath" height=50 style="border-radius: 50%;border: double;">
      </div>
      <div class="recTitle" style="width: 50;display: inline-block;position: fixed;font-weight: 900;">{{ rec.title }}</div>
      <div class="recDesc" style="display: inline-block;width: 150px;position: relative;top: 3px;font-style: italic;font-size: 10px;">{{ rec.desc }}</div>
      <div class="recProgess" style="left: 55px;position: relative;width: 20%;display: inline-block;height: 40px;line-height: 3;"> {{ rec.progress }}</div>
      <mt-button type="primary" class="confirm" style="position: relative;width: 50%;display: inline-block;float: right;color: black;right:10px;background-image:linear-gradient(to right, red, yellow, blue)" @click="success"> 我要加入！</mt-button>
    </div>
  </div>
  <hr>
  <div className="joinedGroup">
    <p style="text-align:center;font-weight: bold;font-size: 30px;">已加入的队伍</p>
    <div class="recruitment" v-for="rec in joined" style="margin-left: 80px;margin-right: 80px;height:105px; border-style: solid;margin-bottom: 50px;">
      <div class="recImg" style="display: inline-block;top: 10px;position: relative;left: 5px;">
        <img :src="rec.imgPath" height=50 style="border-radius: 50%;border: double;">
      </div>
      <div class="recTitle" style="width: 50;display: inline-block;position: fixed;font-weight: 900;">{{ rec.title }}</div>
      <div class="recDesc" style="display: inline-block;width: 150px;position: relative;top: 3px;font-style: italic;font-size: 10px;">{{ rec.desc }}</div>
      <mt-button type="primary" class="confirm" style="position: relative;width: 50%;display: inline-block;float: right;color: black;right:10px;background-image:linear-gradient(to right, red, yellow, blue)" @click="success; popupVisible5 = true"> 查看</mt-button>
    </div>

  </div>
  <mt-popup v-model="popupVisible3" position="right" :modal="false" style="width:100%;height:100%" id="todayTask">
    <mt-header title="2月31日"></mt-header>
    <mt-cell title="任务：自拍一张！让大家看看Loser的模样！"></mt-cell>
    <mt-button @click.native="sheetVisible = true" size="large">上传照片</mt-button>
    <mt-actionsheet
  :actions="actions"
  v-model="sheetVisible">
</mt-actionsheet>
    <mt-button @click.native="popupVisible3 = false;" size="large" type="primary" style="position:fixed;bottom:0px">返回</mt-button>
  </mt-popup>
  <mt-popup v-model="popupVisible5" position="right" :modal="false" style="width:100%;height:100%" id="todayTask2">
    <mt-header title="2月31日"></mt-header>
    <mt-cell title="任务：今天在掌心画一个大大的笑脸吧！就差你了！"></mt-cell>
    <mt-button @click.native="sheetVisible = true" size="large">传张照片吧</mt-button>
    <mt-actionsheet
  :actions="actions2"
  v-model="sheetVisible">
</mt-actionsheet>
    <mt-button @click.native="popupVisible5 = false;" size="large" type="primary" style="position:fixed;bottom:0px;z-index:9999;">返回</mt-button>
  </mt-popup>

  </mt-tab-container-item>
</mt-tab-container>

<mt-tabbar v-model="selected" fixed>
  <mt-tab-item id="bubble">
  <img slot="icon" src="./assets/bubble.svg">
    泡泡
  </mt-tab-item>
  <mt-tab-item id="dungeon">
  <img slot="icon" src="./assets/dungeon.svg">
    Dungeon
  </mt-tab-item>
</mt-tabbar>
</div>
</template>

<script>
import { recruitments, commentList, bubbles, joined } from './mock_data.js'
import { MessageBox } from 'mint-ui';

var popupVisible3 = false;

export default {
  name: 'App',
  data: function() {
    return {
      selected: 'bubble',
      popupVisible: false,
      popupVisible2: false,
      popupVisible3: popupVisible3,
      popupVisible4: false,
      popupVisible5: false,
      inputPopupVisible: false,
      sheetVisible: false,
      initVisible: true,
      hug_cnt: 18,
      recruitments: recruitments,
      joined: joined,
      commentList: commentList,
      bubbles: bubbles,
      task_id: 0,
      value1: []
    }
  },
  methods: {
    send_msg: function (event)
    {
      var div = document.createElement("div")
      div.style.float = "right"
      div.className = "chatMessage"
      div.innerText = document.getElementById("chat").getElementsByTagName("textarea")[0].value
      document.getElementsByClassName("messageList")[0].append(div)
      document.getElementById("chat").getElementsByTagName("textarea")[0].value = "";
      setTimeout(function() { 
        var div = document.createElement("div")
        div.style.float = "left"
        div.className = "chatMessage"
        div.innerText = "巨丧"
        document.getElementsByClassName("messageList")[0].append(div)
      }, 2000);
    },
    blow: function (event)
    {
      this.inputPopupVisible = false;
      var text = document.getElementsByTagName("textarea")[0].value;
      var bubble = document.getElementsByClassName("bubble")[0];
      var new_bubble = document.createElement("div")
      new_bubble.className = "bubble-cell"
      new_bubble.style.animation = "floating " + (2+Math.random() * 10) + "s linear infinite";
      new_bubble.style.left = Math.random() * 300 + "px";
      var p = document.createElement("p");
      p.style.color = "red";
      p.style.marginTop = "30px";
      p.innerText = text;
      document.getElementsByTagName("textarea")[0].value = ""
      new_bubble.append(p);
      bubble.append(new_bubble)
    },
    uploaded2: function (event)
    {
      MessageBox.alert('太好了，团队任务完成了！').then(action => {
        if (document.getElementsByClassName("cong").length == 0) {
                var pass_text = `
        世界依然糟糕，但我们一起做到了：
        - 深深吸口气，然后吐出来，重复三次
        - 默念三遍，「我是独一无二的」。
        - 听一首欢快的歌，快乐在身边。
        - 睡觉前伸一个懒腰，好好睡觉。
        - 好好吃一顿饭。
        - 拍摄朝阳、夕阳、星光或者灯光，黑暗都会过去。
        - 在掌心画一个大大的笑脸。
                `;
                var p = document.createElement("p");
                p.className = "cong";
                p.innerText = pass_text;
                p.style.animation = "floating2 10s linear";
                /*p.style.animationDelay = "-5s"; */
                document.querySelectorAll("[id=todayTask2]")[0].append(p);
         }
      });

    },
    uploaded: function (event)
    {
        if (document.getElementsByClassName("congrats").length == 0)
        {
          var div = document.createElement("div");
          div.className = "congrats";
          div.innerText = "哈哈哈！收到了！明天也请继续来！";
          div.style.textAlign = "center";
          var img = document.createElement("img");
          img.src = require("./assets/congrats.gif");
          img.style.width = "100%";
          div.append(img)
          document.getElementById("todayTask").append(div);
        }
    },
    success: function (event)
    {
      MessageBox.confirm('啊?真要加啊?别吧').then(action => {
        console.log(event.target.TagName)
        if (event.target.parentNode.getElementsByTagName("div")[3].innerText == "4/5")
        {
          this.popupVisible3 = true;
        }
        else {
          event.target.parentNode.getElementsByTagName("div")[3].innerText = "4/5";
          event.target.innerText = "报名了！"
        }
      }, event)
    },
    append: function (event)
    {
      var message = document.getElementById("commentArea").getElementsByTagName("textarea")[0].value;
      commentList.push({
        "user": "路人（我）",
        "message": message
      });

      document.getElementById("commentArea").getElementsByTagName("textarea")[0].value = "";

      setTimeout(function() { document.getElementById("comment_ul").scrollTop = document.getElementById("comment_ul").scrollHeight;}, 500);
    },
    release: function (event)
    {
        
        this.popupVisible = false;
        this.popupVisible2 = false;
        var cells = document.getElementsByClassName("bubble-cell");
        var i = 0;
        for (i=0; i<cells.length; i++)
        {
            if (cells[i].style.animation == "")
            {
                cells[i].style.animation = "floating " + (4+Math.random() * 20) + "s linear infinite";
                cells[i].style.zIndex = "";
            }
        }
    },
    toggle_bubble: function (event)
    {
        var cells = document.getElementsByClassName("bubble-cell")
        var stop_cells = 0;
        var i = 0;
        for (i = 0; i < cells.length; i++)
        {
            if (cells[i].style.animation == "")
            {
                stop_cells++;
            }
        }
        if (event.target.tagName == "DIV") {
            this.popupVisible = true;
            if (event.target.style.animation && stop_cells == 0)
            {
              event.target.style.animation = "";
              event.target.style.bottom = "400px";
              event.target.style.left = "30px";
              event.target.style.zIndex = "9999";
            }
            else
            {
                event.target.style.animation = "floating " + (4+Math.random() * 20) + "s linear infinite";
            }
        }
    }
  },
  created: function() {
    this.options1 = ['抑郁', '焦虑', '压力', '水逆', '不想工作', '厌学', '失恋', '985废物'];
  },
  mounted: function() {
      this.actions = [{
        name: '拍照',
        method: this.uploaded
      }];
      this.actions2 = [{
        name: '上传照片',
        method: this.uploaded2
      }];
      var bubbles = document.getElementsByClassName("bubble-cell")
      var i = 0;
      for (i=0; i<bubbles.length; i++) {
          bubbles[i].style.left = Math.random() * 300 + "px";
          bubbles[i].style.animation ="floating " + (4+Math.floor(Math.random()*20)) + "s linear infinite";
      }
    }
}
</script>

<style>
.mint-tabbar.is-fixed {
    right: 0;
    bottom: 0;
    left: 0;
    position: fixed;
    z-index: 1;
    height: 10%;
}

.mint-tab-item-label {
    color: inherit;
    font-size: 15px;
    line-height: 2;
}

.bubble-cell {
  position: fixed;
  border-radius: 100%;
  width: 100px;
  height: 100px;
  text-align: center;
  background: #bbbbbb;
  border-radius: 50%;
  -webkit-box-shadow: 0 20px 30px rgba(0, 0, 0, 0.2), inset 0px 10px 30px 5px rgba(255, 255, 255, 1);
  -moz-box-shadow: 0 20px 30px rgba(0, 0, 0, 0.2), inset 0px 10px 30px 5px rgba(255, 255, 255, 1);
  box-shadow: 0 20px 30px rgba(0, 0, 0, 0.2), inset 0px 10px 30px 5px rgba(255, 255, 255, 1);
}

@keyframes floating {
  0%{
    transform: translateY(900px);
  }
  100%{
    transform: translateY(-200px);
  }
}

@keyframes floating2 {
  0%{
    transform: translateY(700px);
  }
  100%{
    transform: translateY(0px);
  }
}

#bubble_send {
  width: 100%;
}
.chatMessage {
    top: 10px;
    height: 30px;
    border: solid;
    border-radius: 51%;
    width: 50%;
    text-align: center;
    position: relative;
}
</style>



